body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.game-wrapper {
  width: 800px;
  height: 600px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.grid {
  flex: 1 1;
  display: flex;
  flex-direction: row;
}

.grid-col {
  display: flex;
  flex: 1 1;
  flex-direction: column;
}

.grid-cell {
  flex: 1 1;
  align-items: center;
  justify-content: center;
  display: flex;
  font-size: 28px;
  font-weight: bold;
  border: 1px solid #ccc;
  cursor: pointer;
}

.grid-cell:hover {
  background: #ebebeb;
}

.mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5;
  background: #fff;
}

.game-over-message {
  color: red;
}

.form {
  margin-bottom: 1em;
}

.form-label {
  display: inline-block;
  margin-right: 1em;
}

.numeric-input {
  padding: 1em;
  width: 20px;
}
